import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { Usuario } from '../model/usuario/usuario.model';
import { UsuarioService } from '../services/service.index';
import Swal from 'sweetalert2' ;


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(
    public router:Router,
    public _usuarioService: UsuarioService
  ) { }

  ngOnInit(): void {
    this._usuarioService.cargarStorage();
  }


  ingresar(forma:NgForm){
   
    
    let usuario=new Usuario("","","",forma.value.areaUsuario,"","",forma.value.email,forma.value.password);

    this._usuarioService.login(usuario)
    .subscribe(resp =>
      {
        console.log(usuario);
        if(usuario.areaUsuario ==='administracion'){
          this._usuarioService.cargarStorage();
              this.router.navigate(['/dashboard']);
              console.log(resp);
            } 
        if(usuario.areaUsuario ==='area1'){
          this.router.navigate(['/area1']);
        } 
        
      });
   
  


}


City: any = ['administracion', 'area1']

}
